<template>
  <div class="theme">
    <title-header title="自定义主题"></title-header>
    <div class="box-con">
      <ul>
        <!--错误提示，为li标签添加class="warning"-->
        <li :class="{'warning': sysName.length < 1}">
          <p class="input-title">
            系统名称
          </p>
          <input type="text" placeholder="登录后页面标签显示的系统名称，不超过10个字" maxlength="10" v-model="sysName"/>
          <small>*请输入系统名称（最多10个字）</small>
        </li>
        <li>
          <p class="input-title pt12">
            显示logo
          </p>
          <a class="upload">
            <img src="../../assets/img/upload.png" />
            <input type="file" accept="image/png,image/jpg" id="logo_file_i" @change="changeFile('logo_file_i', 'logo_pre')"/>
          </a>
          <p class="input-title1 pt12">
            预览
          </p>
          <a class="upload">
            <img id="logo_pre" :src="preLogo"/>
          </a>
          <p class="tips">
            用于您的自定义logo设置，登录后显示在主界面左上角。支持 .jpg或 .png格式，图片小于30kb。
          </p>
        </li>
      </ul>
      <div class="cl button">
        <el-button class="blue" type="primary" @click="saveLogoInfo">保存</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import constant from '@/common/constant'
import { getItem } from '@/utils/funcs'
import msgUtil from '@/utils/msgUtil'
import { customInfo } from '@/api/system'
import TitleHeader from '@/components/titleHeader'

export default {
  name: 'Theme',
  components: {
    TitleHeader
  },
  data() {
    return {
      sysName: getItem(constant.companyName) || '应急信息管理平台',
      // preLogo: localStore.getItem(constant.logoMini) ? constant.qiniu_occc_url + localStore.getItem(constant.logoMini) : './static/img/logo.png'
      preLogo: 'http://ts.wu99.nuphone.net/static/img/logo.png'
    }
  },
  methods: {
    changeFile(cur) {
      const file = document.getElementById(cur).files[0]
      if (file) {
        const fileReader = new FileReader()
        fileReader.onload = (e) => {
          if (cur === 'logo_file_i') {
            this.preLogo = e.target.result
          } else {
            this.preNotice = e.target.result
          }
        }
        fileReader.readAsDataURL(file)
      }
    },
    saveLogoInfo() {
      if (this.sysName) {
        const file = document.getElementById('logo_file_i').files[0]
        if (file) {
          const formData = new FormData()
          formData.append('upfile', file)
          formData.append('sys_title', this.sysName)
          customInfo(formData).then(resp => {
            msgUtil.success('保存成功')
          }, error => {
            msgUtil.error('保存失败', msgUtil.findErrorMsg(error))
          })
        } else {
          msgUtil.error('保存失败', '请上传logo图片')
        }
      }
    }
  }
}
</script>

